import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false

// const app = new Vue({
//   render: h => h(App),
// }).$mount('#app')

// if in development mode make app available in window as appWorks
if (process.env.NODE_ENV === 'development') {

  // window.appWorks = app;
  window.app = new Vue({
    render: h => h(App),
  }).$mount('#elmlp-rais')

} else {
  document.addEventListener('DOMContentLoaded', () => {
    //the event occurred
    console.log('elmlp-rais loaded')
    new Vue({
      render: h => h(App),
    }).$mount('#elmlp-rais')
  })

}