<template>
  <div class="filter-box-wrapper">
    <h4>{{ $t(title) }}</h4>
    <!-- <div class="card p-1">
      <h4 @click="open = !open">WOLFGANG - {{ open }}</h4>
    </div> -->
    <div class="card filter-list" :class="{ open: open }">
      <div class="card-text">
        <ul :style="`column-count: ${columns}`">
          <li v-for="(item, index) in filterData" :key="index">
            <label class="custom-checkbox"
              >{{ $t(`${item}`) }}
              <input type="checkbox" :value="item" v-model="checked" />
              <span class="checkmark"></span>
            </label>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { filterGroups } from "../assets/filterData";
export default {
  name: "FilterBox",
  props: {
    value: {
      type: Array,
    },
    title: {
      type: String,
    },
    filterKey: {
      type: String,
    },
    columns: {
      default: 1,
    },
  },
  data() {
    return {
      filterData: filterGroups[this.filterKey],
      checked: [],
      open: true,
    };
  },
  watch: {
    /* eslint-disable */
    checked() {
      this.$emit("input", this.checked);
    },
    value() {
      this.init();
    },
  },
  methods: {
    init() {
      this.checked = this.value; // load default filters (v-model)
    },
  },
  mounted() {
    this.init();
  },
};
</script>

<style lang="scss">
.filter-box-wrapper {
  text-align: left;

  h4 {
    text-transform: capitalize;
    font-family: "Gotham-Medium", Verdana, Arial, sans-serif;
    line-height: 20px;
    font-size: 14px;
    font-weight: bold;
    color: #0a0a0a;
    margin-bottom: 0;
    margin-top: 0.2rem;
  }

  .card {
    border-radius: 0;
    .filter-list {
      overflow: hidden;
      max-height: 0px;
      transition: all 0.2s ease-in-out;
      opacity: 0;
      &.open {
        max-height: 500px !important;
        opacity: 1;
      }
    }
  }
  ul {
    list-style: none;
    margin-left: 0.2rem;
    margin-bottom: 0;
    padding: 0;
    li {
      margin-left: 0.2rem;
    }
  }
}
.custom-checkbox {
  position: relative;
  display: inline-block;
  text-align: left;
  font-family: "Gotham-Book", Verdana, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #0a0a0a;
  line-height: 20px;
  margin: 6px 0;
  padding-left: 22px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  // hide native checkbox
  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  .checkmark {
    position: absolute;
    top: 2px;
    left: 0;
    height: 16px;
    width: 16px;
    border: 2px solid #000;
    border-radius: 3px !important;
    background-color: #fff;
  }
  /* On mouse-over, add a grey background color */
  &:hover input ~ .checkmark {
    background-color: rgb(233, 233, 233);
  }
  /* When the checkbox is checked, add a blue background */
  input:checked ~ .checkmark {
    background-color: #000;
  }
  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the checkmark when checked */
  input:checked ~ .checkmark:after {
    display: block;
  }

  /* Style the checkmark/indicator */
  .checkmark:after {
    left: 4px;
    top: 0px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
}
</style>
