<template>
  <div class="filter-box-wrapper-tg">
    <div class="card">
      <!-- show if there are more than 1 option -->
      <h4
        v-if="filterData.length > 1"
        class="card-header row no-gutters"
        :class="{ open: open }"
        @click="open = !open"
      >
        <div class="col-2 text-center">
          <label class="custom-checkbox">
            <input type="checkbox" :value="filterKey" v-model="checked" />
            <span class="checkmark"></span>
          </label>
        </div>
        <div class="col-8 my-auto cursor">
          <div v-html="titleFormatted"></div>
        </div>
        <div class="col-1 my-auto text-right">
          <b-badge v-if="total > 0">{{ total }}</b-badge>
        </div>
        <div class="col-1 my-auto text-right cursor">
          <svg style="width:24px;height:24px" viewBox="0 0 24 24">
            <path
              fill="currentColor"
              d="M8.59,16.58L13.17,12L8.59,7.41L10,6L16,12L10,18L8.59,16.58Z"
            />
          </svg>
        </div>
      </h4>

      <!-- If there's only 1 option use as title and don't link to dropdown -->
      <h4 class="card-header row no-gutters" v-else>
        <div class="col-2 text-center">
          <label class="custom-checkbox">
            <input type="checkbox" :value="filterData[0]" v-model="checked" />
            <span class="checkmark"></span>
          </label>
        </div>
        <div class="col-8 my-auto cursor">
          <div>{{ $t(filterData[0]) }}</div>
        </div>
      </h4>
      <div class="card-text filter-list" :class="{ open: open }">
        <ul :style="`column-count: ${columns}`">
          <li v-for="(item, index) in filterData" :key="index">
            <label class="custom-checkbox"
              >{{ $t(`${item}`) }}
              <input type="checkbox" :value="item" v-model="checked" />
              <span class="checkmark"></span>
            </label>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { filterGroups } from "../assets/filterData";
export default {
  name: "TradeGroupFilterBox",
  props: {
    value: {
      type: Array,
    },
    title: {
      type: String,
    },
    filterKey: {
      type: String,
    },
    columns: {
      default: 1,
    },
  },
  data() {
    return {
      filterData: filterGroups[this.filterKey],
      checked: [],
      open: false,
    };
  },
  watch: {
    /* eslint-disable */
    checked() {
      //console.log(this.total);
      this.$emit("input", this.checked);
    },
    value() {
      this.init();
    },
  },
  computed: {
    titleFormatted: function() {
      let title = this.$t(`${this.filterKey}`);
      return title.replace(/\//g, "/<br/>");
    },
    total() {
      // total amount select for this filter key
      let filterData = this.filterData;
      let checkedValue = this.checked;
      let _total = filterData.filter((item) => {
        return checkedValue.includes(item);
      });
      return _total.length;
    },
  },
  methods: {
    init() {
      this.checked = this.value; // load default filters (v-model)
    },
  },
  mounted() {
    this.init();
  },
};
</script>

<style lang="scss">
.filter-box-wrapper-tg {
  text-align: left;

  .card {
    border-radius: 0;
    .card-header {
      text-transform: capitalize;
      font-family: "Gotham-Book", Verdana, Arial, sans-serif;
      line-height: 1.2;
      font-size: 14px;
      font-weight: bold;
      color: #0a0a0a;
      padding: 0.75rem 0.25rem !important;
      background: white !important;
      * svg {
        transition: transform 0.2s ease-in-out;
      }

      &.open {
        * svg {
          transform: rotate(90deg) !important;
        }
      }
    }
    .cursor {
      cursor: pointer;
    }
  }
  ul {
    list-style: none;
    margin-left: 0.2rem;
    margin-bottom: 0;
    padding: 0;
    li {
      margin-left: 0.2rem;
    }
  }

  .custom-checkbox {
    position: relative;
    display: inline-block;
    text-align: left;
    font-family: "Gotham-Book", Verdana, Arial, sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #0a0a0a;
    line-height: 20px;
    margin: 6px 0;
    padding-left: 22px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    // hide native checkbox
    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
    }
    .checkmark {
      position: absolute;
      top: 2px;
      left: 0;
      height: 16px;
      width: 16px;
      border: 2px solid #000;
      border-radius: 3px !important;
      background-color: #fff;
    }
    /* On mouse-over, add a grey background color */
    &:hover input ~ .checkmark {
      background-color: rgb(233, 233, 233);
    }
    /* When the checkbox is checked, add a blue background */
    input:checked ~ .checkmark {
      background-color: #000;
    }
    /* Create the checkmark/indicator (hidden when not checked) */
    .checkmark:after {
      content: "";
      position: absolute;
      display: none;
    }

    /* Show the checkmark when checked */
    input:checked ~ .checkmark:after {
      display: block;
    }

    /* Style the checkmark/indicator */
    .checkmark:after {
      left: 4px;
      top: 0px;
      width: 5px;
      height: 10px;
      border: solid white;
      border-width: 0 3px 3px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
    }
  }

  .filter-list {
    overflow: hidden;
    max-height: 0px;
    transition: all 0.2s ease-in-out;
    opacity: 0;
    &.open {
      max-height: 500px !important;
      opacity: 1;
    }
  }
}
</style>
