<template>
  <div
    class="btn-group chart-type btn-group-toggle btn-group-sm"
    data-toggle="buttons"
  >
    <label class="btn" :class="lang === 'en' ? 'btn-dark' : 'btn-outline-dark'">
      <input
        v-model="lang"
        type="radio"
        name="type"
        id="earnings"
        value="en"
        autocomplete="off"
      />
      EN
    </label>
    <label class="btn" :class="lang === 'fr' ? 'btn-dark' : 'btn-outline-dark'">
      <input
        v-model="lang"
        type="radio"
        name="type"
        id="growth"
        value="fr"
        autocomplete="off"
      />
      FR
    </label>
  </div>
</template>

<script>
export default {
  name: "langChanger",
  data() {
    return {
      lang: "en",
    };
  },
  watch: {
    lang(newVal) {
      //console.log("lang", this, newVal);
      this.$i18n.locale = newVal;
    },
  },
};
</script>

