    seriesNameNodata() {
      // returns legend of names with no data

      // Generates All Possible Filter Combinations Based on searchFilters
      const _this = this;
      let seriesName = this.seriesName; // array of current names existing in legend
      let searchFilters = this.searchFilters;
      let cohort = searchFilters.cohort;
      let RS_trade = searchFilters.RS_trade;
      let sex = searchFilters.sex;

      // function which creates all combinations from an array of muliple arrays
      function cartesian(...args) {
        var r = [],
          max = args.length - 1;
        function helper(arr, i) {
          for (var j = 0, l = args[i].length; j < l; j++) {
            var a = arr.slice(0); // clone arr
            //a.push(args[i][j]);
            let translate = _this.$t(args[i][j]);
            a.push(translate);
            if (i == max) r.push(a);
            else helper(a, i + 1);
          }
        }
        helper([], 0);

        return r;
      }
      let fullLegend = cartesian(cohort, RS_trade, sex); // is legend of all possible combinations based on search fitlers
      fullLegend = fullLegend.map((item) => item.join(" - "));

      fullLegend = fullLegend.filter(function(el) {
        return seriesName.indexOf(el) < 0;
      });
      //console.log("possibleFilters", fullLegend);
      return fullLegend;
    },
    download() {
      // creates csv download for current chart
      let translatedData = JSON.parse(JSON.stringify(this.filteredData));

      translatedData = translatedData.map((item) => {
        item.cohort = this.$t(item.cohort);
        item.RS_type = this.$t(item.RS_type);
        item.RS_trade = this.$t(item.RS_trade);
        item.sex = this.$t(item.sex);
        return item;
      });
      return {
        data: translatedData,
        //data: this.filteredData,
        header: {
          cohort: { title: this.$t("cohort") },
          RS_type: { title: this.$t("RS_type") },
          RS_trade: { title: this.$t("RS_trade") },
          sex: { title: this.$t("sex") },
          ysg: { title: this.$t("ysg") },
          earnings: { title: this.$t("earnings") },
          N: { title: this.$t("N") },
          growth: { title: this.$t("growth") },
        },
        fileName: this.$t(this.chartType),
      };
    },
    alertBoxLive() {
      // checks to see alert box is required for filter selection
      let alertBox = this.alertBox;
      let searchFilters = this.searchFilters;

      alertBox.forEach((item) => {
        item.show =
          searchFilters.RS_trade.includes(item.condition) &&
          (searchFilters.sex.includes("Male") ||
            searchFilters.sex.includes("Female"));
      });
      return;
    },
