      //let _slider = document.getElementById("vertical-range-slider");
      let _slider = this.$refs["vertical-range-slider"];
      let chartType = this.chartType;

      if (_slider.noUiSlider) {
        _slider.noUiSlider.destroy();
      }
      let rangeSlider = noUiSlider.create(
        _slider,
        this.rangeSliderOptions[this.chartType]
      );

      // adjust range slider options based on chart type
      this.rangeSliderOptions[chartType].format.to = this.formatValue;

      if (range) {
        // if range exists set range
        rangeSlider.set(range);
      }

      // When the slider value changes, update the input and span
      _slider.noUiSlider.on("update", function(values, handle) {
        values = values || null;
        _this.updateYaxis(values);
      });
    },
    getData() {
      // returns data based on search filters
      let data = this.raisData;
      let _filteredData = data.filter((obj) => {
        return (
          this.searchFilters["RS_trade"].indexOf(obj["RS_trade"]) !== -1 &&
          this.searchFilters["cohort"].indexOf(obj["cohort"]) !== -1 &&
          this.searchFilters["sex"].indexOf(obj["sex"]) !== -1
        );
      });
      this.filteredData = _filteredData;
      this.formatData(_filteredData);
    },
    formatData(data2Format) {
      // formats data to usable series for Apex Charts
      const dataObj = {};
      let chartType = this.chartType;
      let series = data2Format.forEach((item) => {
        let name = JSON.stringify(item); // stringifies name which will be parsed for tooltip later

        // GENERATES LEGEND NAME which will be used to sort data
        let sortKey = `${this.$t(item.cohort)} - ${this.$t(
          item.RS_trade
        )} - ${this.$t(item.sex)}`;

        console.log("item.growth", item.growth);
        // FORMAT Y-AXIS VALUE
        let yaxisValue = 0;
        if (chartType === "earnings") {
          yaxisValue = parseFloat(item[chartType]); // if earnings use earnings value
        } else if (chartType === "growth") {
          yaxisValue = parseFloat(item.growth)
            ? parseFloat(item.growth) * 100
            : 0; // if growth multiply by 100 and use growth value
        }

        let data = {
          x: Number(item.ysg),
          y: yaxisValue,
          N: item.N,
        };

        if (!dataObj[sortKey]) {
          let Obj = {
            name: name,
            data: [data],
            sortKey: sortKey,
          };
          dataObj[sortKey] = Obj;
        } else {
          dataObj[sortKey]["data"].push(data);
        }
      });

      // CONVERTS DATA OBJECT TO ARRAY THAN SORTS SERIES
      let formattedData = Object.keys(dataObj)
        .map((key) => dataObj[key]) // converts data object to array
        .sort((a, b) => (a.sortKey > b.sortKey ? 1 : -1)); // sort by key

      console.log("series", formattedData);
      // DOE SOMETHING WITH DATA
      this.series = formattedData;
    },
    formatTooltip(seriesName, chart) {
      let name = JSON.parse(seriesName);
      let series = chart.w.config.series[chart.seriesIndex];
      let dataIndex = chart.dataPointIndex;
      let ysg = series.data[dataIndex].x;
      let N = series.data[dataIndex].N;
      //console.log("series data", series.data);
      let tooltip = "";
      let valueTitle = this.$t(this.chartType);
      const header = {
        cohort: name.cohort,
        sex: this.$t(name.sex),
        RS_type: this.$t(name.RS_type),
        RS_trade: this.$t(name.RS_trade),
        ysg: ysg,
        Count: this.$n(N),
      };
      Object.keys(header).forEach((item) => {
        let title = this.$t(item);
        tooltip += `<strong>${title}: </strong>${header[item]}<br/>`;
      });
      tooltip += `<strong>${valueTitle}: </strong>`;

      return tooltip;
    },
    updateYaxis: function(range) {
      range = range.map((item) =>
        Number(
          item
            .replace(/\s/g, "")
            .replace("$", "")
            .replace("%", "")
            .replace(",", "")
        )
      );

      //console.log(range);

      this.chartOptions = {
        ...this.chartOptions,
        ...{
          yaxis: {
            show: true,
            showAlways: true,
            min: range[0],
            max: range[1],
            tickAmount: 10,
            seriesName: "Earning Level",
            opposite: false,
            reversed: false,
            logarithmic: false,
            forceNiceScale: false,
            floating: false,
            decimalsInFloat: 4,
            labels: {
              show: true,
              align: "right",
              minWidth: 0,
              maxWidth: 160,
              style: {
                color: undefined,
                fontSize: "14px",
                fontFamily: "Gotham-Medium",
                cssClass: "apexcharts-yaxis-label",
              },
              offsetX: 0,
              offsetY: 0,
              rotate: 0,
              formatter: (value) => {
                let formatValue = this.formatValue;

                return formatValue(value);
              },
            },
          },
        },
      };
    },
    formatValue(value) {
      // formats dollar value and/or percentage based on language
      let locale = this.$i18n.locale;
      let chartType = this.chartType;
      value = value.toFixed(0); // rounds number up 1.57 wll be 2
      //value = parseInt(value); // rounds number up 1.57 wll be 2
      //console.log("value", value);
      if (chartType === "earnings") {
        let frValue = new Intl.NumberFormat("fr-CA", {
          style: "currency",
          currency: "CAD",
        })
          .format(value)
          .replace(/,00/gi, "") // remove ,00 from french
          .replace("CA", ""); //  remove CA from currency

        let enValue = new Intl.NumberFormat("en-CA", {
          style: "currency",
          currency: "CAD",
        })
          .format(value)
          .replace(/\D00$/, "");
        value = locale == "en" ? enValue : frValue; // based on en or fr
      } else {
        // else if growth
        value = `${value} %`;
      }
      return value;
    },
    reRenderChart() {
      console.log("render chart");
      this.renderChart = false;
      this.$nextTick(() => {
        this.renderChart = true;
        this.init();
      });
    },
    checkAlertBox() {
      // checks to see alert box is required for filter selection
      let alertBox = this.alertBox;
      let searchFilters = this.searchFilters;

      alertBox.forEach((item) => {
        item.show =
          searchFilters.RS_trade.includes(item.condition) &&
          (searchFilters.sex.includes("Male") ||
            searchFilters.sex.includes("Female"));
      });
    },
    resetFilters() {
      this.searchFilters = {
        sex: ["Both sexes"],
        cohort: ["2009"],
        RS_trade: ["All trades combined"],
      };
    },
    resetSlider() {
      // RESETS RANGE SLIDER TO DEFAULT VALUES
      let _slider = this.$refs["vertical-range-slider"];

      _slider.noUiSlider.reset();
    },
    // SAVE IMAGE FUNCTIONS
    getChartImage: function() {
      // screen shot of chart element
      // deselect any times
      this.deselectAll;
      const chartType = this.chartType;
      const imageLabel = this.$i18n.t(`${chartType}`).replace(" ", "_");
      //let chartFrame = document.querySelector("#chart-wrapper");
      let chartFrame = this.$refs["chart-wrapper"];

      function filter(node) {
        return node.id !== "vertical-range-slider";
      }

      // image URL test
      domtoimage
        .toPng(chartFrame)
        .then(function(dataUrl) {
          console.log("dataUrl", dataUrl);
        })
        .catch(function(error) {
          console.error("oops, something went wrong!", error);
        });

      domtoimage
        .toBlob(chartFrame, { filter: filter })
        .then(function(blob) {
          window.saveAs(blob, imageLabel + ".png");
        })
        .catch(function(error) {
          console.error("oops, something went wrong!", error);
        });
    },
    deselectAll: function() {
      // used for deselection to hide cursor before image is saved (getChartImage())
      if (window.getSelection) {
        window.getSelection().removeAllRanges();
      } else if (document.selection) {
        document.selection.empty();
      }
    },
    // CHART URL PARAMETER FUNCTIONS
    getRangeSliderValues() {
      // get range slider values
      let rangeSlider = this.$refs["vertical-range-slider"];
      let range = rangeSlider.noUiSlider.get();

      let rangeMin = range[0].replace(/[!@#$%^&*,\s]/gi, "");
      let rangeMax = range[1].replace(/[!@#$%^&*,\s]/gi, "");

      let rangeSliderValues = [
        parseInt(rangeMin).toFixed(0),
        parseInt(rangeMax).toFixed(0),
      ];
      console.log("getRange", this.$i18n.locale, rangeSliderValues);

      return rangeSliderValues; // array of numbers
    },
    generateChartUrl() {
      //console.log("generat url", this.$i18n.locale);
      //generates chart share url
      // activated only when share button is pressed
      const chartParams = this.searchFilters;

      // get range slider values
      let rangeSliderValues = this.getRangeSliderValues();

      chartParams["chartType"] = this.chartType; // set chart type
      chartParams["range"] = rangeSliderValues; // set range value
      chartParams["lang"] = this.$i18n.locale; // set current language
      let search = window.location.search;
      let baseUrl = window.location.href.replace(search, "");

      let url = encodeURI(JSON.stringify(chartParams));
      let paramId = this.copyUrl.paramId;

      this.copyUrl.url = `${baseUrl}?${paramId}=${url}`;
    },
    checkUrl() {
      // CHECK URL IF CHART PRESETS/PARAMS EXISTS
      let _this = this;
      let paramId = this.copyUrl.paramId;

      this.currentUrl = window.location.href;
      let url = new URL(this.currentUrl);
      console.log("checkurl", this);
      let chartParams = url.searchParams.get(paramId);

      if (chartParams) {
        //console.log("checkUrl", chartParams);
        try {
          chartParams = JSON.parse(chartParams);
        } catch (err) {
          console.log("could not parse presets", err);
          return;
        }
        history.replaceState(
          { page: 1 },
          "Rais Dashboard",
          window.location.pathname
        );
        this.loadChartPresets(chartParams);
      } else {
        // to regular initialization
        //console.log("check url init");
        const regex = /\/fr(\/|$)/; // language regex
        this.$i18n.locale = regex.test(url) ? "fr" : "en";
        _this.init();
      }
    },
    loadChartPresets(chartParams) {
      // loads chart presets from url

      // // CHECK TO MAKE SURE ALL REQUIRED PARAMS EXISTS
      let requiredKeys = ["chartType", "range", "lang"];
      let searchFilters = this.searchFilters;

      let searchFilterKeys = Object.keys(searchFilters);

      requiredKeys = [...requiredKeys, ...searchFilterKeys]; // combines arrays

      // check if params have all required keys/values
      let paramsOk = requiredKeys.every((key) =>
        Object.keys(chartParams).includes(key)
      );

      if (!paramsOk) {
        // stop of all required params don't exist and initialize as normal
        this.init();
        console.log("Bad formatted params");
        return;
      }

      let chartType = chartParams.chartType;
      let range = chartParams.range;
      this.$i18n.locale = chartParams.lang; // set language
      console.log("chartParams", chartParams);
      // remove
      delete chartParams.chartType;
      delete chartParams.range;
      delete chartParams.lang;
      console.log("chartParams", chartParams);
      // set search params & chart type
      this.chartType = chartType;
      this.searchFilters = chartParams;

      // set range slider
      range = [parseInt(range[0]), parseInt(range[1])];
      console.log("loadpresets");
      // once presets are loaded get data and genera
      this.init(range);
    },
    copyToClipboard() {
      const _this = this;

      // copys chart url to clipboard
      //let targetCodeToCopy = document.querySelector("#chart-url");
      let targetCodeToCopy = this.$refs["chart-url"];
      targetCodeToCopy.setAttribute("type", "text"); // 不是 hidden 才能複製
      targetCodeToCopy.select();

      const copyAlert = function(msg, type) {
        console.log("copy alert");
        const copyUrl = _this.copyUrl;
        type = type ? type : "warning";
        msg = msg ? msg : "An error has occurred";
        copyUrl.type = type;
        copyUrl.msg = msg;
        copyUrl.showModal = 2;
        setTimeout(() => {
          copyUrl.showModal = 0;
        }, 2000);
      };

      try {
        var success = document.execCommand("copy");
        var msg = success
          ? "Copied to clipboard"
          : "Could not copy to clipboard";
        copyAlert(msg, "success");
      } catch (err) {
        copyAlert("error");
      }

      /* unselect the range */
      // targetCodeToCopy.setAttribute("type", "hidden");
      // window.getSelection().removeAllRanges();
      this.deselectAll();
    },
  },
  mounted() {
    this.$nextTick(() => {
      console.log("this refs", this.$refs);
      this.checkUrl();
    });
  },
  created() {
    this.debug = this.$attrs.debug ? true : false; // check if debug attribute exists
  },
  i18n: i18n,
};
</script>

